@import url(https://fonts.googleapis.com/css2?family=Teko:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.screen{
    font-family: 'Teko', sans-serif;
    font-weight: 700;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    font-size: 2rem;
    height: 30px;
    border: none;
    background-color: rgb(60, 146, 204);
    border: none;
    color: white;
    text-align: right;
    padding-right: 20px;
    padding-left: 10px;
    padding-top: 10px;
    padding-bottom: 20px;
}
.calculator{
    border: 1px solid #cccccc;
    box-shadow: 10px 10px 30px 0px rgba(0,0,0,0.75);
    border-radius: 20px;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    width: 450px;
    height:600px;
}
.keys{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap:10px;
    padding: 10px;
    margin: auto;
}
.button{
    height: 60px;
    padding: 5px;
    background-color: #fff;
    border-radius: 5px;
    border: 1px solid #c4c4c4;
    background-color: transparent;
    font-size: 2rem;
    color: #333;
    background-image: linear-gradient(to bottom, transparent, transparent 50%, rgba(0,0,0,0.04));
    box-shadow: inset 0 0 0 1px rgba(255 255, 255, .05), inset 0 1px 0 0 rgba(255, 255, 255, .45), inset 0 -1px 0 0 rgba(255, 255,255, .15), 0 1px ;
    text-shadow: 0 1px rgba(255 255, 255, .4);
}

.button:hover{
    background-color: #eaeaea;
}

.operator{
 color: #fff;
 background-color: #eebb24;
}

.clear{
    background-color: #f0595f;
    border-color: #b0353a;
    color: #fff;
}
.clear:hover{
    background-color: #f17377;
}
.equal-sign{
    background-color: #25a8e0;
    border-color: #25a8e0;
    color: #fff;
}
.equal-sign:hover{
    background-color: #4e9ed4;
}
